.webaudio-starter {
	@import 'options-output';
	@import 'code-output';
	@import 'tip';
	.options-output {
		.option {
			figure {
				display: inline-block;
				vertical-align: text-top;
				margin: 0 -1px 0 0;
				padding: 0;
				canvas {
					border: 1px solid;
				}
			}
			figure:last-of-type {
				margin: 0;
			}
		}
	}
}
